.modal-content.bg {
  background: #fff;
  max-width: 500px;
  padding: 40px; }
  .modal-content.bg .close {
    top: 10px;
    right: 10px; }
    .modal-content.bg .close svg {
      fill: #000; }
  .modal-content.bg p {
    line-height: 1.4; }

#app #area-map {
  height: 100%; }
  #app #area-map .categories-list .category .cat-link {
    font-size: 0.885rem; }
    @media only screen and (min-width: 1900px) {
      #app #area-map .categories-list .category .cat-link {
        font-size: 1.15rem; } }
  @media (min-width: 768px) and (max-width: 1899px) {
    #app #area-map .mapContainer {
      height: 634px;
      min-height: 634px; } }



